<template>
   <div class="setmsg">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>实名认证</span>
        </div>
        <div class='set_cen'>
            <div class='set_tit'>
                身份信息
            </div>
            <van-cell-group>
                <van-field 
                label="昵称"
                disabled
                :placeholder="nickName" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                label="证件类型"
                disabled
                placeholder="身份证" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                v-model="idcard"
                label="证件号码"
                placeholder="请输入证件号码" />
            </van-cell-group>
            <div class='set_tit'>
                账户信息
            </div>
            <van-cell-group>
                <van-field 
                label="真实姓名"
                v-model="realname"
                placeholder="请输入真实姓名" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                v-model="bankacount"
                label="银行卡账号"
                placeholder="请输入银行卡账号" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                v-model="phone"
                label="预留手机号"
                placeholder="请输入预留手机号" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                v-model='area'
                label="开户地区"
                placeholder="请输入开户地区" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                v-model="bank"
                label="开户银行"
                placeholder="请输入开户银行" />
            </van-cell-group>
            <van-cell-group>
                <van-field
                v-model="branch" 
                label="联行名称"
                placeholder="请输入联行名称" />
            </van-cell-group>
            <div class='set_tit'>
                支付信息
            </div>
            <van-cell-group>
                <van-field 
                v-model="paypasswd"
                type="password"
                label="交易密码"
                placeholder="请输入交易密码" />
            </van-cell-group>
        </div>
        <van-button size="large" class='set_but' @click='submit()'>认证</van-button>
   </div>
</template>

<script>
import axios from 'axios';
export default {
   name: 'setmsg',
   data() {
       return {
            nickName:localStorage.nickName,
            paypasswd:'',
            idcard:'',
            realname:'',
            bankacount:'',
            phone:'',
            area:'',
            bank:'',
            branch:''
       }
   },
   created(){
   },
   methods:{
        retreat(){
            this.$router.go(-1);
        },
        submit(){
            let that = this;
                let url = window.globalData.config.domain + '/userLogin/authenticate';
                axios.post(url,{
                    cmd:'102',
                    body:{
                        accessToken: localStorage.token,
                        paypasswd: this.paypasswd,
                        idcard: this.idcard,
                        realname: this.realname,
                        bankacount: this.bankacount,
                        phone: this.phone,
                        area: this.area,
                        bank: this.bank,
                        branch: this.branch
                    }
                }).then((res) => {
                    this.verify(res);
                })
        },
        verify(res){
            let err = res.data.errmsg;
            if( err === 'missing parameters' ){
                this.$dialog.alert({
                    message: '请填写完整信息！'
                })
            }else if( err === 'realname format wrong' ){
                this.$dialog.alert({
                    message: '请填写正确姓名！'
                })
            }else if( err === 'bankacount format wrong' ){
                this.$dialog.alert({
                    message: '请填写正确银行卡号！'
                })
            }else if( err === 'paypasswd is wrong' ){
                this.$dialog.alert({
                    message: '交易密码不正确！'
                })
            }else if( err === 'idcard already exist' ){
                this.$dialog.alert({
                    message: '该身份证已经注册！'
                })
            }else if( err === 'bankacount already exist' ){
                this.$dialog.alert({
                    message: '银行卡号已经被注册！'
                })
            }else if( err === 'authenticate can only beset once' ){
                this.$dialog.alert({
                    message: '每个账户只能实名认证一次！'
                })
            }else{
                this.$dialog.alert({
                    message: '实名认证成功！'
                }).then( () => {
                    this.$router.go(-1);
                })
            }
        }
   },
}
</script>

